import _ from 'lodash'

import Table from '../../mixins/table'

import route from './route'

export default {
  name: route.key,

  mixins: [
    Table
  ],

  data() {
    return {
      action: null,
      list: null,
      subAgent: {
        id: null,
        account_no: null,
        username: '',
        name: '',
        lname: '',
        commission: 0,
        status: 1,
        password: ''
      },
      rules: {
        username: [
          {
            required: true,
            message: 'Please input username',
            trigger: 'blur'
          }
        ],
        name: [
          {
            required: true,
            message: 'Please input first name',
            trigger: 'blur'
          }
        ],
        lname: [
          {
            required: true,
            message: 'Please input last name',
            trigger: 'blur'
          }
        ],
        password: [
          {
            required: true,
            message: 'Please input password',
            trigger: 'blur'
          }
        ]
      },
      showDialog: false,
      isLoading: true,
      isFetching: false,
      isSaving: false,
      route,
    }
  },

  computed: {
    data() {
      let search = this.search.trim().toUpperCase()
      let data = this.list ? this.list.data.filter(d => {
        return !search
          || (
               d.account_no.indexOf(search) > -1
            || d.full_name.trim().toUpperCase().indexOf(search) > -1
            || d.username.trim().toUpperCase().indexOf(search) > -1
          )
      }) : []

      return this.sort.order ?
        _.orderBy(data, [this.sort.prop], [this.sort.order])
          : data
    },

    dialogTitle() {
      return this.action == 'add' ?
        'Add new sub-agent' : 'Edit sub-agent'
    }
  },

  mounted() {
    this.fetch()
  },

  methods: {
    handleClose(done) {
      this.action = null
      this.subAgent = {
        id: null,
        account_no: null,
        username: '',
        name: '',
        lname: '',
        commission: 0,
        status: 1,
        password: ''
      }
      this.$refs.form.resetFields()

      if (typeof done == 'function') {
        done()
      }
    },

    openDialog(action) {
      this.action = action
      this.showDialog = true
    },

    closeDialog() {
      this.handleClose()
      this.showDialog = false
    },

    async fetch() {
      try {
        this.list = await this.$store.dispatch('subagent/list')
      } catch(e) {
        this.$message({
          type: 'error',
          message: e.message
        })
      }

      this.isLoading = false
    },

    async remove(id) {
      try {
        await this.$store.dispatch('subagent/remove', id)

        this.fetch()
        this.$message({
          type: 'success',
          message: 'Delete Successful'
        })
      } catch(e) {
        this.$message({
          type: 'error',
          message: 'Unable to remove.'
        })
      }
    },

    async edit(id) {
      this.isLoading = true

      try {
        this.subAgent = await this.$store.dispatch('subagent/get', id)
      } catch(e) {
        this.$message({
          type: 'error',
          message: e.message
        })
      }

      this.isLoading = false
      this.openDialog('edit')
    },

    async save() {
      if (this.isSaving) return

      this.$refs.form.validate(async valid => {
        if (!valid || this.isSaving) {
          return
        }

        this.isSaving = true

        try {
          delete this.subAgent.account_no
          delete this.subAgent.pa_id

          await this.$store.dispatch('subagent/save', this.subAgent)

          this.fetch()
          this.closeDialog()
          this.$message({
            type: 'success',
            message: this.action == 'add' ? 'Add Successful' : 'Update Successful'
          })
        } catch(e) {
          this.$message({
            type: 'error',
            message: e.message
          })
        }

        this.isSaving = false
      })
    }
  }
}



